  // russian

  @font-face {
    font-family: 'Helvetica-55';
    src: url('../fonts/russian/helvetica-55/HelveticaNeueCyr-Roman.eot');
    /* IE9 Compat Modes */
    src: url('../fonts/russian/helvetica-55/HelveticaNeueCyr-Roman.eot') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/russian/helvetica-55/HelveticaNeueCyr-Roman.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/russian/helvetica-55/HelveticaNeueCyr-Roman.woff') format('woff'), /* Pretty Modern Browsers */
    url('../fonts/russian/helvetica-55/HelveticaNeueCyr-Roman.ttf') format('truetype'), /* Safari, Android, iOS */
    /* Legacy iOS */
  }
  @font-face {
    font-family: 'Helvetica-65';
    src: url('../fonts/russian/helvetica-65/HelveticaNeueCyr-Medium.eot');
    /* IE9 Compat Modes */
    src: url('../fonts/russian/helvetica-65/HelveticaNeueCyr-Medium.eot') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/russian/helvetica-65/HelveticaNeueCyr-Medium.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/russian/helvetica-65/HelveticaNeueCyr-Medium.woff') format('woff'), /* Pretty Modern Browsers */
    url('../fonts/russian/helvetica-65/HelveticaNeueCyr-Medium.ttf') format('truetype'), /* Safari, Android, iOS */
    /* Legacy iOS */
  }
    @font-face {
    font-family: 'Helvetica-75';
    src: url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.eot');
    /* IE9 Compat Modes */
