    url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.woff') format('woff'), /* Pretty Modern Browsers */
    url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.ttf') format('truetype'), /* Safari, Android, iOS */
    // url('../fonts/helvetica-75/helvetica-neue-lt-geo-75-bold.svg') format('svg');
    /* Legacy iOS */
  }
  @font-face {
    font-family: 'Helvetica-75-bold';
    src: url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.eot');
    /* IE9 Compat Modes */
    src: url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.eot') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.woff2') format('woff2'), /* Super Modern Browsers */
    url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.woff') format('woff'), /* Pretty Modern Browsers */
    url('../fonts/russian/helvetica-75/HelveticaNeueCyr-Bold.ttf') format('truetype'), /* Safari, Android, iOS */
